@font-face{
    font-family: 'instagram-sans';
    src : url('./Assets/Fonts/Instagram-Sans.ttf');
}

@font-face{
    font-family: 'instagram-sans-headline';
    src : url('./Assets/Fonts/Instagram-Sans-Headline.ttf');
}

.display-large {
    font-size: 2.4rem;
    line-height: 1.5;
    font-weight: 800;
    letter-spacing: 0.1rem;
    max-width: 90vw;
    display: block;
    font-family: "instagram-sans-headline";
}

